import { committees } from "./committees";
import { Disclosure } from "@headlessui/react";
import { ChevronDownIcon } from "@heroicons/react/24/outline";
function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const Committees = ({ country }) => {
  const countryCommittees = committees.find((obj) => obj.country === country);

  return (
    <div className="relative bg-gray-900">
      <div className="relative h-56 bg-indigo-600 sm:h-72 md:absolute md:left-0 md:h-full md:w-1/2">
        <img
          className="h-full w-full object-cover"
          src="https://www.iaeste.at/wp-content/uploads/DSC_0517-1536x1020.jpg"
          alt=""
        />
        <div
          aria-hidden="true"
          className="absolute inset-0 bg-gradient-to-r from-teal-500 to-cyan-600 mix-blend-multiply"
        />
      </div>
      <div
        className="relative mx-auto max-w-md px-4 py-12 sm:max-w-7xl sm:px-6 sm:py-20 md:py-28 lg:px-8 lg:py-32"
        style={{ paddingTop: "3rem", paddingBottom: "3rem" }}
      >
        <div className="md:ml-auto md:w-1/2 md:pl-10">
          <h2
            className="text-lg font-semibold text-gray-300"
            style={{ fontSize: "1.5rem" }}
          >
            Cities with IAESTE LCs
          </h2>
          <p className="mt-2 text-lg font-bold  text-white ">
            In {country.replace(/-/g, " ")} we have five local commitees:
          </p>
          <p className="mt-3 text-3xl text-gray-300 sm:text-4xl">
            <ul>
              {countryCommittees.lcs.length <= 5 &&
                countryCommittees.lcs.map((committee) => {
                  return <li key={committee}>LC {committee}</li>;
                })}
              {countryCommittees.lcs.length > 5 &&
                countryCommittees.lcs.map((com, index) => {
                  if (index <= 5) {
                    return <li key={com}>{com}</li>;
                  }
                })}
              {countryCommittees.lcs.length > 5 && (
                <Disclosure as="div" className="pt-6">
                  {({ open }) => (
                    <>
                      <dt className="text-lg">
                        <Disclosure.Button className="flex w-full items-start justify-between text-left text-gray-400">
                          <span className="font-medium text-white-900">
                            See more
                          </span>
                          <span className="ml-6 flex h-7 items-center">
                            <ChevronDownIcon
                              className={classNames(
                                open ? "-rotate-180" : "rotate-0",
                                "h-6 w-6 transform"
                              )}
                              aria-hidden="true"
                            />
                          </span>
                        </Disclosure.Button>
                      </dt>
                      <Disclosure.Panel as="dd" className="mt-2 pr-12">
                        {countryCommittees.lcs.map((com, index) => {
                          if (index > 5) {
                            return <li key={com}>{com}</li>;
                          }
                        })}
                      </Disclosure.Panel>
                    </>
                  )}
                </Disclosure>
              )}
            </ul>
            <p style={{ fontSize: "2rem", paddingTop: "1rem" }}>
              You can also visit IAESTE {country} here:
            </p>
            <a href={countryCommittees.page}>{countryCommittees.page}</a>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Committees;
