import React from "react";
import { food, drinks } from "./dataFood";
import FoodCard from "./FoodCard";

const Food = ({ country, foodRef }) => {
  console.log(country);
  const countryFood = food.find((obj) => obj.country === country);
  console.log(countryFood);
  const countryDrinks = drinks.find((obj) => obj.country === country);

  return (
    <section style={{ padding: "0 2rem", textAlign: "center" }} ref={foodRef}>
      <h2>Food</h2>
      <div className="grid grid-cols-3 gap-4" style={{ marginBlock: "2rem" }}>
        {countryFood &&
          countryFood.content.map((item, index) => {
            return (
              <FoodCard
                key={`food${index}`}
                title={item.title}
                description={item.description}
              />
            );
          })}
      </div>
      <h2>Drinks</h2>
      <div className="grid grid-cols-3 gap-4">
        {countryDrinks &&
          countryDrinks.content.map((item, index) => {
            return (
              <FoodCard
                key={`drink${index}`}
                title={item.title}
                description={item.description}
              />
            );
          })}
      </div>
    </section>
  );
};

export default Food;
