import { countries } from "./countries";
import classes from "./Countries.module.css";
import img from "./IAESTElogos/IAESTE_AUSTRIA.png";

const Countries = ({ passRef }) => {
  return (
    <div className="bg-white" ref={passRef} id="countries-div">
      <div className="mx-auto max-w-2xl py-16 px-4 sm:py-24 sm:px-6 lg:max-w-7xl lg:px-8">
        <div style={{ display: "flex", gap: "4rem" }}>
          <h2 className="text-2xl font-bold  text-gray-900 ">
            Entering:
          </h2>
          <input
            type="text"
            placeholder="Search for a country"
            style={{
              padding: "0.5rem",
              paddingLeft: "1rem",
              border: "1px solid black",
              borderRadius: "10px",
              width: "400px",
            }}
          />
        </div>
        <div className="mt-12 grid grid-cols-1 gap-y-10 gap-x-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 xl:gap-x-8">
          {countries.map((country) => (
            <a key={country.id} href={country.href} className="group">
              <div className="aspect-w-1 aspect-h-1 w-full overflow-hidden rounded-lg bg-white-200 xl:aspect-w-7 xl:aspect-h-7 left-14">
                <img
                  src={country.imageSrc}
                  // src={img}
                  alt={country.imageAlt}
                  className={`h-full w-full object-cover object-center group-hover:opacity-75 ${classes.imgHover}`}
                />
              </div>
              {/* <h3 className="mt-4 text-sm text-gray-700">{country.name}</h3> */}
            </a>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Countries;
