import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { ArrowCircleLeft, ArrowCircleRight } from "heroicons-react";
import { files } from "./gallery";

function Arrow(props) {
  let className =
    props.type === "prev"
      ? " -left-8 mt-1 text-white z-50 h-32 inset-y-0 w-8 text-2xl text-center  cursor-pointer hover:opacity-80 transition duration-300 ease-in-out hover:scale-105"
      : " mt-1  -right-9 inset-y-0 text-white  z-10 h-32 inset-y-0 w-8 text-2xl text-center  cursor-pointer hover:opacity-80 transition duration-300 ease-in-out hover:scale-105 ";

  const char =
    props.type === "next" ? (
      <ArrowCircleRight className=" absolute right-2 h-[7.5rem] w-[1rem] lg:-mt-52 sm:-mt-[23.7rem] text-gray-500   " />
    ) : (
      <ArrowCircleLeft className=" absolute left-2 h-[7.5rem] w-[1rem] lg:mt-80 sm:mt-[21rem] text-gray-500" />
    );

  return (
    <div className={className} onClick={props.onClick}>
      {char}
    </div>
  );
}

const Gallery = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (
    <div>
      <h4 className="text-3xl font-bold  text-gray-900 sm:text-5xl sm:leading-none lg:text-4xl ml-32 mb-24 mt-24 ">
        Gallery
      </h4>
      <Slider
        className=""
        prevArrow={<Arrow type="prev" onClick={undefined} />}
        nextArrow={<Arrow type="next" onClick={undefined} />}
        {...settings}
      >
        {/* <ul role="list" className="grid grid-cols-1 gap-x-4 gap-y-8 sm:grid-cols-3 sm:gap-x-6 lg:grid-cols-3 xl:gap-x-8 lg:mx-32 mx-5"> */}
        {files.map((file) => (
          <li key={file.source} className="relative">
            <div className="flex justify-center">
              <img
                src={file.source}
                alt=""
                className="lg:w-[52rem] lg:h-[36rem] w-[19.5rem] h-56"
              />
            </div>
          </li>
        ))}
        {/* </ul> */}
      </Slider>
    </div>
  );
};

export default Gallery;
